<template>
    <div>
      <base-header></base-header>
      <hr/>
      <div class="container">
        <div class="log-possion">
          <h1 class="log-font">注册</h1>
            用户名:<input type="text"  class="con-input"  placeholder="请输入用户名" />
            <br/>
            密码：<input type="password"  class="con-input"  placeholder="请输入密码" />
            <br/>
            确认密码：<input type="password"  class="con-input"  placeholder="请再次输入密码" />
          <br/>
            <form  class="choice-box">
              <label>角色选择： </label>
              <label><input name="role" type="radio" value="" />用户 </label>
              <label><input name="role" type="radio" value="" />商家 </label>
              <label><input name="role" type="radio" value="" />管理员 </label>
            </form >
            <button class="login_btn" v-on:click="login">注册</button>
        </div>
      </div>

      <hr/>
      <base-footer></base-footer>

    </div>

</template>

<script>
  import baseHeader from '@/components/common/BaseHeader.vue'
  import baseFooter from '@/components/common/BaseFooter.vue'
    export default {
        name: "UserRegister",
      // blogHeader/blogFooter组件给申明到components里面然后在template里面使用
      components: { baseHeader, baseFooter },

      methods:{
          login(){
            this.$router.push('/login')
          }
      }
    }
</script>

<style scoped>
  .container {
    width: 980px;
    height: 600px;
    margin: 0 auto;
  }
  .log-possion{
    width: 980px;
    position: fixed;
    top: 250px;
    border: cornflowerblue;
  }
  .log-font{
    font-family: 华文行楷;
  }
  .con-input {
    height: 40px;
    width: 20%;
    margin-bottom: 5px;
    padding-left: 10%;
    border: 0;
    border-bottom: solid 1px lavender;
  }
  .login_btn {
    width: 10%;
    font-size: 16px;
    filter: brightness(1.4);
  }
</style>



