import Vue from 'vue'
import Router from 'vue-router'
import UserLogin from '@/components/manage/UserLogin.vue'
import UserIndex from '@/components/home/UserIndex.vue'
import UserRegister from "@/components/manage/UserRegister";
import UserMyCart from "@/components/manage/UserMyCart";


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/index'
    },
    {
      path: '/index',
      name: 'UserIndex',
      component: UserIndex
    },
    {
      path: '/manage',
      redirect: '/index'
    },
    {
      path: '/login',
      name: 'UserLogin',
      component: UserLogin
    },
    {
      path: '/UserRegister',
      name: 'UserRegister',
      component: UserRegister
    },
    {
      path: '/UserMyCart',
      name: 'UserMyCart',
      component: UserMyCart
    },

  ]
})



