        <br/>
        密码：<input type="password"  class="con-input" v-model="loginInfoVo.password" placeholder="请输入密码" />
        <br/>
        <form  class="choice-box">
          <label>角色选择： </label>
          <label><input name="role" type="radio" value="" />用户 </label>
          <label><input name="role" type="radio" value="" />商家 </label>
          <label><input name="role" type="radio" value="" />管理员 </label>
        </form >
        <button class="login_btn" v-on:click="login">登录</button>
