    components: { baseHeader, baseFooter },
    data () {
      return {
        loginInfoVo: { username: '', password: '' },
        responseResult: []
      }
    },
    methods: {

      register(){
        this.$router.push('/UserRegister')
      },

      login () {
        this.$router.push('/index')

        //
